import {RequestApi as request} from "../api";
import _ from 'lodash'
export const GetDoctorSearch = (data) => {
    // console.log("data+++++++++++++")
    var urls = '';

    Object.keys(data).map(function (key,index){
        if (data[key] !== '')
        {
            console.log(" lenght : " ,Object.keys(data).length , " / index" , index)
            if (index === (Object.keys(data).length)-1)
            {
                urls = urls + key + "="+data[key];
            }else{
                urls = urls + key + "="+data[key]+"&";
            }
        }
        // console.log(key , " => " ,url)
    })


    if (data !== null && data !== "") {
        if (data.grade && data.sort && data.gender) {
            urls = "grade="+data.grade + "&" + "sort="+data.sort+ "&" + "gender="+data.gender;
        } else if (data.grade && !data.sort && data.gender) {
            urls = "sort="+data.grade;
        } else if (!data.grade && data.sort) {
            urls = "sort="+data.sort;
        }
    }

    console.log("url : " , urls )
    return async (dispatch, getState) => {
        await request.SearchApi(urls)
            .then(response => {
                dispatch({
                    type: 'SEARCH_SUCCESS',
                    response: response
                })
            },error => {
                dispatch({
                    type : 'EMPTY_DATA',
                    response : 401
                })
            })
    }
}

