import React, {memo, useEffect, useState} from "react";
import $ from "jquery";
import M from "materialize-css";
import {GetDoctorSearch} from "../../../entity/Redux/Micro/SearchDoctor/Actions";
import {useDispatch, useSelector} from "react-redux";
import {useTranslation} from "react-i18next";
import {SECRET_USER_DATA, TOKEN_KEY} from "../../../services/Authentication/Types";
import {FILE_CODE} from "./../../../services/Connection/Types";
import {FitDataAssistant} from "../../../entity/Redux/Auth/SignUp/Action";

export default memo((props) => {
    const dispatch = useDispatch();
    const resUpload = useSelector((state) => state.Auth.resLog);
    const dataUser = JSON.parse(
        localStorage.getItem(SECRET_USER_DATA + "dataUser")
    );

    console.log("token : ", localStorage.getItem(TOKEN_KEY))
    console.log("data old : ", dataUser);
    const [addDataUser, setAddDataUser] = useState(dataUser);
    const [dataSearch, setDataSearch] = useState({});
    useEffect(() => {
        let eleman = document.querySelectorAll(".materialboxed");
        M.Materialbox.init(eleman);
        var selectes = document.querySelector("select");
        M.FormSelect.init(selectes);
        search();
        if (resUpload) {
            M.toast({
                html: "your data save successfully , please wait to redirect to panel",
            });
            setTimeout(() => {
                window.location.pathname = "/doctor";
            }, 1500);
        }
    }, []);

    const {i18n} = useTranslation();

    function handleLang(e) {
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", "en");
        }
        var mini = $(".mini-box");
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === "en") {
            $(".mini-box #one").css({display: "none"});
            $(".mini-box #tow").css({display: "flex"});
            $(".box_switch .one img").css({display: "block"});
            $(".box_switch .tow img").css({display: "none"});
            mini.addClass("actives");
            localStorage.setItem("lan", "ch");
        } else {
            $(".mini-box #tow").css({display: "none"});
            $(".mini-box #one").css({display: "flex"});
            $(".box_switch .one img").css({display: "none"});
            $(".box_switch .tow img").css({display: "block"});
            mini.removeClass("actives");
            localStorage.setItem("lan", "en");
        }
        let lang = localStorage.getItem("lan");
        i18n.changeLanguage(lang);
    }

    const MicroFirst = useSelector((state) => state.Micro);

    $(".closeee").click(function () {
        $(".back_show").fadeOut();
        $("input[name=checkbox_hiden]").attr("checked", true);
    });

    function search(state = "") {
        dispatch(GetDoctorSearch(state));
    }

    function searchData(e) {
        let filetr = {...dataSearch};
        filetr.sort = e.target.value;
        setDataSearch(filetr);
        console.log("data searchsssss : ", dataSearch)
        // action GetDoctorSearch ra ba vorodi dataSearch dispatch mikone ta search ro bar asas inputhai karbar anjam bedeh
        search(filetr);
    }

    // list doctor => baray namayesh estefade mishe
    const [doctorsList, setDoctorsList] = useState({dataUser, doctors: []});

    // assistand doctor entehab mikone zira bayad tavasot yek doctor entekhab shavad va dasyar on bashe
    function handleChange(e, id) {
        if ($("#" + id).prop("checked")) {
            setDoctorsList({
                ...doctorsList,
                doctors: [...doctorsList.doctors, id],
            });
        } else {
            var lists = [...doctorsList.doctors];
            var index = lists.indexOf(id);
            if (index !== -1) {
                lists.splice(index, 1);
                setDoctorsList({
                    doctors: lists,
                });
            }
        }
    }

    // meghdar val = doctor grade ke ba select option daryaft mishe
    // bad az daryaft va set dar state be action ferestade mishe ta dispatch beshe
    function handleSelectGrade(val) {
        let gradeEdit = {...dataSearch};
        gradeEdit.grade = val;
        setDataSearch(gradeEdit); // meghdar jadid state ke update mishe
        console.log("gender : ", gradeEdit, " / response : ", dataSearch)
        search(gradeEdit);
    }

    // meghdar val = doctor gender ke ba select option daryaft mishe
    // bad az daryaft va set dar state be action ferestade mishe ta dispatch beshe
    function handleSelectGender(val) {
        let genderEdit = {...dataSearch};
        genderEdit.gender = val;
        setDataSearch(genderEdit); // meghdar jadid state ke update mishe
        // console.log("grade : ", genderEdit, " / respo : ", dataSearch)
        search(genderEdit);
    }

    function handleForm(e) {
        const latestdata = {...addDataUser};
        if (localStorage.getItem(FILE_CODE)) {
            let filePaths = localStorage.getItem(FILE_CODE);
            let fileUrl = JSON.parse(filePaths).data.data;
            latestdata.doctors = doctorsList;
            latestdata.attachment = fileUrl;
            dispatch(FitDataAssistant(latestdata));
            //   console.log("data : ", latestdata);
        } else {


        }

        //
    }

    return (
        <div id={"main_search_doctor"}>
            <div className={"box-search"}>
                <div className={"search-tab"}>
                    <div className={"row"}>
                        <div className={"col s12 l6 m6"}>
                            <p>
                                <span id={"sorts"}>Sort By :</span>
                                <input
                                    name="sort"
                                    onChange={(e) => searchData(e)}
                                    id="doctor-radio"
                                    className="with-gap"
                                    value="commision"
                                    type="radio"
                                />
                                <label htmlFor="doctor-radio">Commission</label>
                                &nbsp;&nbsp;&nbsp;
                                <input
                                    name="sort"
                                    className="with-gap"
                                    value="grade"
                                    onChange={(e) => searchData(e)}
                                    id="admin-radio"
                                    type="radio"
                                />
                                <label htmlFor="admin-radio">Grade</label>
                            </p>
                        </div>

                        <div className={"col s12 l6 m6"}>
                            <div
                                className=" col s12 l4"
                                style={{
                                    color: "#fff",
                                    height: 35,
                                    marginTop: "5px",
                                    marginBottom: 15,
                                }}
                            >
                                <select
                                    onChange={(e) => handleSelectGender(e.target.value)}
                                    className={"browser-default"}
                                    name="gender_sel"
                                >
                                    <option value={''} selected>
                                        search by gender
                                    </option>
                                    <option value={"male"}>Male</option>
                                    <option value={"female"}>FeMale</option>
                                    <option value={"other"}>Other</option>
                                </select>
                            </div>

                            <div
                                className=" col s12 l4"
                                style={{
                                    color: "#fff",
                                    height: 35,
                                    marginTop: "5px",
                                    marginBottom: 15,
                                }}
                            >
                                <select
                                    onChange={(e) => handleSelectGrade(e.target.value)}
                                    className={"browser-default"}
                                    name="grade_sel"
                                >
                                    <option value={''} selected>
                                        search by grade
                                    </option>
                                    <option value={"3"}>3</option>
                                    <option value={"5"}>5</option>
                                    <option value={"7"}>7</option>
                                </select>
                            </div>

                            <div
                                className={"col s12 l3"}
                                style={{
                                    marginBottom: 15,
                                    display: "flex",
                                    justifyContent: "center",
                                    color: "#fff",
                                }}
                            >
                                <div style={{marginTop: 8}} className="box_switch">
                                    <input
                                        onClick={handleLang}
                                        name="cahngelan"
                                        checked={
                                            localStorage.getItem("lan") === "en" ? false : true
                                        }
                                        type="checkbox"
                                        id="switcher"
                                    />
                                    <div
                                        className={`mini-box ${
                                            localStorage.getItem("lan") === "ch" ? "actives" : ""
                                        }`}
                                    >
                    <span
                        id="one"
                        style={{
                            display: `${
                                localStorage.getItem("lan") === "en" ? "flex" : "none"
                            }`,
                        }}
                    >
                      En
                    </span>
                                        <span
                                            id="tow"
                                            style={{
                                                display: `${
                                                    localStorage.getItem("lan") === "ch" ? "flex" : "none"
                                                }`,
                                            }}
                                        >
                      Ch
                    </span>
                                    </div>
                                    <span className="one">
                    <img
                        style={{
                            display: `${
                                localStorage.getItem("lan") === "en"
                                    ? "none"
                                    : "block"
                            }`,
                        }}
                        src="/img/icons/china.png"
                    />
                  </span>
                                    <span className="tow">
                    <img
                        style={{
                            display: `${
                                localStorage.getItem("lan") === "ch"
                                    ? "none"
                                    : "block"
                            }`,
                        }}
                        src="/img/icons/united-kingdom.png"
                    />
                  </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={"col s12"} style={{position: "relative"}}>
                    {MicroFirst.searchRes == 401 ? (
                        <p
                            style={{
                                textAlign: "center",
                                marginTop: 50,
                                fontSize: 26,
                                color: "#ffffff",
                            }}
                        >
                            data is empty! please try again later ...
                        </p>
                    ) : (
                        <div>
                            <table>
                                <thead>
                                <tr>
                                    <th>Doctor image</th>
                                    <th>Doctor Name</th>
                                    <th>Education Degree</th>
                                    <th>Education Field</th>
                                    <th>Actions</th>
                                </tr>
                                </thead>

                                <tbody>
                                {MicroFirst.searchRes.data
                                    ? MicroFirst.searchRes.data.data.map((item) => {
                                        return (
                                            <tr>
                                                {console.log("data search : ", item)}

                                                <td
                                                    style={{
                                                        alignItems: "center",
                                                        display: "flex",
                                                        justifyContent: "center",
                                                    }}
                                                >
                                                    {item.filePath ? (
                                                        <img
                                                            src={item.filePath}
                                                            alt="bicon img doctor search .img"
                                                            width={50}
                                                            height={50}
                                                            className="materialboxed circle responsive-img"
                                                        />
                                                    ) : (
                                                        <img
                                                            src={"/img/avatar/avatar-11.png"}
                                                            alt="bicon img doctor search .img"
                                                            width={50}
                                                            height={50}
                                                            className="materialboxed circle responsive-img"
                                                        />
                                                    )}
                                                </td>
                                                <td>{item.fullName}</td>
                                                <td>{item.educationDegree}</td>
                                                <td>{item.educationField}</td>

                                                <td>
                                                    <p
                                                        style={{
                                                            display: "flex",
                                                            alignContent: "center",
                                                            justifyContent: "center",
                                                        }}
                                                    >
                                                        <div>
                                                            <input
                                                                name={"checkbox_" + item.id}
                                                                type="checkbox"
                                                                onChange={(e) => handleChange(e, item.id)}
                                                                id={item.id}
                                                            />
                                                            <label htmlFor={item.id}></label>
                                                        </div>
                                                    </p>
                                                </td>
                                            </tr>
                                        );
                                    })
                                    : "please wait ..."}
                                </tbody>
                            </table>

                            <div className={"col s12"}>
                                <div
                                    className={"col s3"}
                                    style={{padding: 15, marginLeft: 15}}
                                >
                                    <button
                                        onClick={(e) => handleForm(e)}
                                        type={"submit"}
                                        className={"btn waves-effect blue darken-4"}
                                    >
                                        submit
                                    </button>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
});
